
export interface Adventure {
    name : string,
    sections : AdventureSection[]
}

export type AdventureSection = ASRichText | ASMultimedia | ASCards | ASTask | ASMapLocation | ASUnlockCards

export interface ASRichText {
    type: "richtext",
    content : string
}

type ASMultimedia = ASImage | ASYoutubeVideo

export interface ASImage {
    type: "image",
    src : string,
    alt : string,
}

export interface ASYoutubeVideo {
    type: "ytvideo",
    url: string,
}

export interface ASCards {
    type: "cards",
    ids : string[]
}

type ASTask = ASShowAnswerTask | ASMultichoiceTask | ASIntegerTask

export interface ASShowAnswerTask {
    type: "showanswertask",
    task : string,
    answer : string
}

export interface ASMultichoiceTask {
    type: "multichoicetask",
    task : string,
    answers : string[],
    correct : boolean[]
}

export interface ASIntegerTask {
    type: "integertask",
    task : string,
    correct : number
}

export interface ASMapLocation {
    type: "maplocation",
    x : number,
    y : number,
}

export interface ASUnlockCards {
    type: "unlockcards",
    ids : string[],
}

