
import React from "react";
import { Adventure, AdventureSection, ASShowAnswerTask, ASImage, ASCards, ASRichText, ASMultichoiceTask, ASIntegerTask, ASMapLocation, ASUnlockCards, ASYoutubeVideo } from "./Model";
import { cards } from "./data/cards/CardsCombined";


export function AdventureSectionEditor(props : {index : number, adventure : Adventure, setAdventure : (newAdventure : Adventure) => void}) {

    const setSection = (i : number, newSections : AdventureSection[], amt : number = 1) => {

        const adventureSectionCopy = [...props.adventure.sections];
    
        adventureSectionCopy.splice(i, amt, ...newSections);
    
        props.setAdventure({
            ...props.adventure,
            sections: adventureSectionCopy
        });
    };

    const deleteSection = () => {
        if (window.confirm("Are you sure you want to delete this section?")) {
            setSection(props.index, []);
        }
    }

    const swapAfter = (i : number) => {
        if (i < 0 || i >= props.adventure.sections.length - 1) {
            return;
        }

        const section1 = props.adventure.sections[i];
        const section2 = props.adventure.sections[i + 1];

        setSection(i, [section2, section1], 2);
    };


    const addNew = () => {

        setSection(props.index + 1, [{
            type: "richtext",
            content: "Content"
        }], 0);
    };

    const sectionTypeTexts : {[typeId in AdventureSection["type"]]: string} = {
        richtext: "Rich Text",
        image: "Image",
        ytvideo: "Youtube Video",
        cards: "Display Cards",
        showanswertask: "Show-answer Task",
        multichoicetask: "Multiple-choice Task",
        integertask: "Integer Task",
        maplocation: "Map Location",
        unlockcards: "Unlock Cards",
    };

    const emptyAdventureSection : {[typeId in AdventureSection["type"]]: AdventureSection} = {
        richtext: {
            type: "richtext",
            content: "Content",
        },
        image: {
            type: "image",
            src: "",
            alt: "Alternate text"
        },
        ytvideo: {
            type: "ytvideo",
            url: "",
        },
        cards: {
            type: "cards",
            ids: [],
        },
        showanswertask: {
            type: "showanswertask",
            task: "Task",
            answer: "Answer",
        },
        multichoicetask: {
            type: "multichoicetask",
            task: "Task",
            answers: [],
            correct: [],
        },
        integertask: {
            type: "integertask",
            task: "Task (answer 0 - 1000)",
            correct: 0,
        },
        maplocation: {
            type: "maplocation",
            x: 0,
            y: 0,
        },
        unlockcards: {
            type: "unlockcards",
            ids: [],
        },
    };

    const section = props.adventure.sections[props.index];

    const changeType = (tpe : AdventureSection["type"]) => {
        if (tpe !== section.type) {
            setSection(props.index, [emptyAdventureSection[tpe]]);
        }
    }

    return (
        <div>
            <div className="specificEditor">
                <SpecificEditor section={section} setSection={(newSection) => setSection(props.index, [newSection])}></SpecificEditor>
            </div>
            <div className="generalEditor">
                <button onClick={(e) => deleteSection()}>Delete this section</button>
                <button onClick={(e) => swapAfter(props.index - 1)}>Move up</button>
                <button onClick={(e) => swapAfter(props.index)}>Move down</button>
                <button onClick={(e) => addNew()}>New section</button>
                <select value={section.type} onChange={(e) => changeType(e.target.value as AdventureSection["type"])}>
                    {Object.keys(sectionTypeTexts).map((tpe) => <option key={tpe} value={tpe}>{sectionTypeTexts[tpe as AdventureSection["type"]]}</option>)}
                </select>
            </div>
        </div>
    );
}

function ListEditor<T>(props : {data : T[], setData : (newData : T[]) => void, IndividualEditor : React.FunctionComponent<{data : T, setData : (newData : T) => void}>, newElem : () => T}) {
        
    const setDataAt = (index : number, newElems : T[], toRemove : number = 1) => {
        const dataCopy = [...props.data];
        
        dataCopy.splice(index, toRemove, ...newElems);

        props.setData(dataCopy);
    }

    const removeElem = (index : number) => {
        setDataAt(index, [], 1);
    }

    const addElementAfter = (index : number) => {
        setDataAt(index + 1, [props.newElem()], 0);
    }

    const swapAfter = (index : number) => {
        if (index < 0 || index >= props.data.length - 1) {
            return;
        }

        setDataAt(index, [props.data[index + 1], props.data[index]], 2);
    }

    const setElem = (index : number, newData : T) => {
        setDataAt(index, [newData], 1);
    }

    return (
        <div>
            <button onClick={(e) => addElementAfter(-1)}>New element</button>
            <div className="listEditor">
                {props.data.map((val : T, n) => <div key={n} className="listEditorElem">
                    <props.IndividualEditor data={val} setData={(newData) => setElem(n, newData)}></props.IndividualEditor>
                    <div className="listEditorElemControls">
                        <button onClick={(e) => removeElem(n)}>Delete this element</button>
                        <button onClick={(e) => swapAfter(n - 1)}>Move up</button>
                        <button onClick={(e) => swapAfter(n)}>Move down</button>
                        <button onClick={(e) => addElementAfter(n)}>New element</button>
                    </div>

                </div>)}
            </div>
        </div>
    );
}

function SpecificEditor(props : {section : AdventureSection, setSection : (newSection : AdventureSection) => void}) {

    const displays : {[key in AdventureSection["type"]]: () => JSX.Element} = {
        "richtext": () => <EditorRichText section={props.section as ASRichText} setSection={props.setSection}/>,
        "image": () => <EditorImage section={props.section as ASImage} setSection={props.setSection}/>,
        "ytvideo": () => <EditorYoutubeVideo section={props.section as ASYoutubeVideo} setSection={props.setSection}/>,
        "cards": () => <EditorCards section={props.section as ASCards} setSection={props.setSection}/>,
        "showanswertask": () => <EditorShowAnswerTask section={props.section as ASShowAnswerTask} setSection={props.setSection}/>,
        "multichoicetask": () => <EditorMultiChoiceTask section={props.section as ASMultichoiceTask} setSection={props.setSection}/>,
        "integertask": () => <EditorIntegerTask section={props.section as ASIntegerTask} setSection={props.setSection}/>,
        "maplocation": () => <EditorMapLocation section={props.section as ASMapLocation} setSection={props.setSection}/>,
        "unlockcards": () => <EditorUnlockCards section={props.section as ASUnlockCards} setSection={props.setSection}/>,
    }

    return (
        <div className="specificEditor">
            {displays[props.section.type]()}
        </div>
    );
}

function EditorRichText(props : {section : ASRichText, setSection : (newSection : ASRichText) => void}) {

    return (
        <div>
            Content: <br />
            <textarea value={props.section.content} onChange={(e) => props.setSection({...props.section, content: e.target.value})} /> <br />
        </div>
    );
}

function EditorImage(props : {section : ASImage, setSection : (newSection : ASImage) => void}) {

    return (
        <div>
            Source link: <br />
            <input type="text" value={props.section.src} onChange={(e) => props.setSection({...props.section, src: e.target.value})} /> <br />
            Alternate text: <br />
            <input type="text" value={props.section.alt} onChange={(e) => props.setSection({...props.section, alt: e.target.value})} /> <br />

        </div>
    );
}

function EditorYoutubeVideo(props : {section : ASYoutubeVideo, setSection : (newSection : ASYoutubeVideo) => void}) {

    return (
        <div>
            Source youtube url: <br />
            <input type="text" value={props.section.url} onChange={(e) => props.setSection({...props.section, url: e.target.value})} /> <br />
        </div>
    );
}

function CardIDEditor(props : {data : string, setData : (newData : string) => void}) {

    return (
        <div>
            <input list="cards-datalist" value={props.data} onChange={(e) => props.setData(e.target.value)} style={{width: "400px"}}/>
            <datalist id="cards-datalist">
                {Object.keys(cards).map((id) => <option key={id} value={id}>{id} ({cards[id]?.name})</option>)}
            </datalist>
        </div>
    );
}

function CardIDListEditor(props : {ids : string[], setIDs : (newIDs : string[]) => void}) {


    return (
        <ListEditor<string> data={props.ids} setData={props.setIDs} IndividualEditor={CardIDEditor} newElem={() => Object.keys(cards)[0]} />
    );

}

function EditorCards(props : {section : ASCards, setSection : (newSection : ASCards) => void}) {

    return (
        <div>
            <CardIDListEditor ids={props.section.ids} setIDs={(ids) => props.setSection({...props.section, ids: ids})}></CardIDListEditor>
        </div>
    );
}

function EditorShowAnswerTask(props : {section : ASShowAnswerTask, setSection : (newSection : ASShowAnswerTask) => void}) {

    return (
        <div>
            Task: <br />
            <textarea value={props.section.task} onChange={(e) => props.setSection({...props.section, task: e.target.value})} /> <br />
            Answer: <br />
            <textarea value={props.section.answer} onChange={(e) => props.setSection({...props.section, answer: e.target.value})} /> <br />
        </div>
    );
}

type MultiChoiceElem = {text : string, isCorrect : boolean}

function EditorMultiChoiceTaskElem(props : {data : MultiChoiceElem, setData : (newData : MultiChoiceElem) => void}) {

    return (
        <div>
            Choice: <br />
            <textarea value={props.data.text} onChange={(e) => props.setData({...props.data, text: e.target.value})} /> <br />
            Correct: 
            <input type="checkbox" checked={props.data.isCorrect} onChange={(e) => props.setData({...props.data, isCorrect: e.target.checked})}/> <br />
        </div>
    );
}

function EditorMultiChoiceTask(props : {section : ASMultichoiceTask, setSection : (newSection : ASMultichoiceTask) => void}) {

    const data = props.section.answers.map((text, i) => ({
        text: text,
        isCorrect: props.section.correct[i]
    }));

    const setData = (newData : MultiChoiceElem[]) => {
        const newAnswers = newData.map((mce) => mce.text);
        const newCorrect = newData.map((mce) => mce.isCorrect);

        props.setSection({
            ...props.section,
            answers: newAnswers,
            correct: newCorrect,
        });
    };

    //console.log(props.section);
    //console.log(data);

    return (
        <div>
            Task: <br />
            <textarea value={props.section.task} onChange={(e) => props.setSection({...props.section, task: e.target.value})} /> <br />
            Choices: <br />
            <ListEditor data={data} setData={setData} IndividualEditor={EditorMultiChoiceTaskElem} newElem={() => ({text: "Choice", isCorrect: false})}></ListEditor>
        </div>
    );
}

function EditorIntegerTask(props : {section : ASIntegerTask, setSection : (newSection : ASIntegerTask) => void}) {

    return (
        <div>
            Task: <br />
            <textarea value={props.section.task} onChange={(e) => props.setSection({...props.section, task: e.target.value})} /> <br />
            Answer: <br />
            <input type="number" step={1} min={0} max={1000} value={props.section.correct} onChange={(e) => props.setSection({...props.section, correct: e.target.valueAsNumber})} /> <br />
        
        </div>
    );
}

function EditorMapLocation(props : {section : ASMapLocation, setSection : (newSection : ASMapLocation) => void}) {

    return (
        <div>
            Position X: <br />
            <input type="number" step={1} min={-100} max={100} value={props.section.x} onChange={(e) => props.setSection({...props.section, x: e.target.valueAsNumber})} /> <br />
            Position Y: <br />
            <input type="number" step={1} min={-100} max={100} value={props.section.y} onChange={(e) => props.setSection({...props.section, y: e.target.valueAsNumber})} /> <br />
        
        </div>
    );
}

function EditorUnlockCards(props : {section : ASUnlockCards, setSection : (newSection : ASUnlockCards) => void}) {

    return (
        <div>
            <CardIDListEditor ids={props.section.ids} setIDs={(ids) => props.setSection({...props.section, ids: ids})}></CardIDListEditor>
        </div>
    );
}
