import React, {useEffect, useState, useRef} from 'react';
import {mapFeatures, MapFeature, MapCity} from './MapFeatures';
import mapImage from './mapImage/map.png';
import cityIcon from './mapImage/city.png';
import placerIcon from './mapImage/placer.png';
import {cards} from './../data/cards/CardsCombined';

export function TopMapSVGWithCamera(props : {width : number, height : number, markedLocation : null | [number, number]}) {

	const [mapZoomData, ] = useState({
		panX: 0,
		panY: 0,
		zoomLevel: 0,
		mouseOriginX: 0,
		mouseOriginY: 0,
		isDown: false,
	}); 

	const canvas = useRef<HTMLCanvasElement>(null);

	// Window height minus the height of the importance button, minus margin
	const height = props.height;

	// Window height minus the height of the importance button, minus margin
	const width = props.width;

	const zoomBase = 1.02;

	useEffect(() => {

		const mapImageImg = new Image();
		mapImageImg.src = mapImage;

		const cityIconImg = new Image();
		cityIconImg.src = cityIcon;

		const placerIconImg = new Image();
		placerIconImg.src = placerIcon;

		let clickables : {x: number, y: number, w: number, h: number, onClick : () => void}[] = [];

		const updateCanvas = () => {
			clickables = [];
			if (canvas.current) {
				canvas.current.width = width;
				canvas.current.height = height;

				const ctx = canvas.current.getContext("2d");


				if(!ctx) {
					return;
				}

				ctx.clearRect(0, 0, width, height);

				ctx.fillStyle = "#7070ff";
				ctx.fillRect(0, 0, width, height);

				// Goal: make viewport -100 -100 200 200 while preserving aspect ratio

				const smallAxis = Math.min(width, height);
				const axisDiff = width - height;
				ctx.translate(Math.max(0, axisDiff / 2), Math.max(0, -axisDiff / 2));
				ctx.scale(smallAxis / 200, smallAxis / 200);


				ctx.translate(100, 100); // Making (0, 0) the center

				ctx.translate(mapZoomData.panX, mapZoomData.panY);

				const zoom = Math.pow(zoomBase, mapZoomData.zoomLevel);
				ctx.scale(zoom, zoom);


				ctx.drawImage(mapImageImg, -100, -100, 200, 200);

				if (props.markedLocation !== null) {
					const [x, y] = props.markedLocation;

					const placerWidth = 10;
					const placerHeight = 13;

					ctx.translate(x, y);
					ctx.scale(1/zoom, 1/zoom);

					ctx.drawImage(placerIconImg, -placerWidth / 2, -placerHeight, placerWidth, placerHeight);

					ctx.scale(zoom, zoom);
					ctx.translate(-x, -y);
				}

				// TODO: Add on click action
				for (const mapFeature of mapFeatures) {
					if (mapFeature.type === "city") {

						
						const mapCity = mapFeature;

						const x = mapCity.x;
						const y = mapCity.y;

						if (cards[mapCity.cardID] === undefined) {
							continue; 
						}
						
						const isVisible = true; //props.mapdata.cardVisibility[mapCity.cardID]


						ctx.translate(x, y);
						ctx.scale(1/zoom, 1/zoom);

						const text = isVisible ? mapCity.name : "?";

						//ctx.drawImage(cityIconImg,  - 2,  - 2, 4, 4);
						ctx.fillStyle = "#ffffff";
						ctx.strokeStyle = "#000000";
						ctx.lineWidth = 0.5; 
						ctx.beginPath();
						ctx.arc(0, 0, 1, 0, 2 * Math.PI);
						ctx.fill();
						ctx.stroke();

						ctx.font = "3px Arial"; // This must be before measuring
						const textMeasure = ctx.measureText(text);

						console.log(text + " was measured to width: " + textMeasure.width);

						ctx.fillStyle = "rgba(0, 0, 0, 0.3)";
						ctx.fillRect( - (textMeasure.width + 2) / 2,  - 5.5, (textMeasure.width + 2), 3);

						ctx.fillStyle = "white";
						ctx.fillText(text,  - textMeasure.width / 2,  - 3);

						ctx.scale(zoom, zoom);
						ctx.translate(-x, -y);

						/*

						const onClickAction = isVisible ? (() => {props.mapdata.gotoLink(mapCity.cardID)}) : (() => {props.mapdata.uncover(mapCity.cardID)});

						clickables.push({
							x: x - (textMeasure.width + 2) / 2,
							y: y - 5.5,
							w: (textMeasure.width + 2),
							h: 5.5 + 2,
							onClick: onClickAction
						});

						*/
					}
				}
			}
			
		};

		mapImageImg.addEventListener("load", () => {
			updateCanvas();
		});

		const transformMouseCoordsToCorrectCoords = (e : MouseEvent) => {

			const canvasRectX = canvas.current?.getBoundingClientRect()?.x || 0;
			const canvasRectY = canvas.current?.getBoundingClientRect()?.y || 0;

			const mx = e.clientX - canvasRectX;
			const my = e.clientY - canvasRectY;

			/*
			Transformations of the canvas:
			// Goal: make viewport -100 -100 200 200 while preserving aspect ratio

			const smallAxis = Math.min(width, height);
			const axisDiff = width - height;
			ctx.translate(Math.max(0, axisDiff / 2), Math.max(0, -axisDiff / 2));
			ctx.scale(smallAxis / 200, smallAxis / 200);


			ctx.translate(100, 100); // Making (0, 0) the center
			*/

			const smallAxis = Math.min(width, height);
			const axisDiff = width - height;

			const firstTranslateX = Math.max(0, axisDiff / 2);
			const firstTranslateY = Math.max(0, -axisDiff / 2);

			const scale = smallAxis / 200;

			const secondTranslateX = 100;
			const secondTranslateY = 100;

			const nx = (mx - firstTranslateX) / scale - secondTranslateX;
			const ny = (my - firstTranslateY) / scale - secondTranslateY;

			return {
				x: nx,
				y: ny
			};
		}

		const transformMouseCoordsToCorrectCoordsWithCamera = (e : MouseEvent) => {
			const {x: posX, y: posY} = transformMouseCoordsToCorrectCoords(e);

			return {
				x: (posX - mapZoomData.panX) / Math.pow(zoomBase, mapZoomData.zoomLevel),
				y: (posY - mapZoomData.panY) / Math.pow(zoomBase, mapZoomData.zoomLevel)
			};
		}

		if (canvas.current) {

			// Zooming with wheel
			canvas.current.addEventListener("wheel", (e) => {
				e.preventDefault();


				const localClientAbsPoint = transformMouseCoordsToCorrectCoords(e);
				const localClientXAbs = localClientAbsPoint.x;
				const localClientYAbs = localClientAbsPoint.y;

				const oldZoom = mapZoomData.zoomLevel;
				const deltaZoom = e.deltaY > 0 ? -1 : 1;
				const newZoom = oldZoom + deltaZoom;

				const deltaZoomMult = Math.pow(zoomBase, newZoom) - Math.pow(zoomBase, oldZoom);
				
				const oldZoomMult = Math.pow(zoomBase, oldZoom);

				const localClientX = (localClientXAbs - mapZoomData.panX) / oldZoomMult;
				const localClientY = (localClientYAbs - mapZoomData.panY) / oldZoomMult;

				const offsetX = -localClientX * deltaZoomMult;
				const offsetY = -localClientY * deltaZoomMult;

				const newX = mapZoomData.panX + offsetX;
				const newY = mapZoomData.panY + offsetY;

				mapZoomData.zoomLevel = newZoom;
				mapZoomData.panX = newX;
				mapZoomData.panY = newY;

				updateCanvas();
			});

			// The next four are dragging the canvas
			canvas.current.addEventListener("mousedown", (e) => {
				e.preventDefault();
				const {x: posX, y: posY} = transformMouseCoordsToCorrectCoords(e);
				mapZoomData.mouseOriginX = posX - mapZoomData.panX;
				mapZoomData.mouseOriginY = posY - mapZoomData.panY;
				mapZoomData.isDown = true;
			});

			canvas.current.addEventListener("mouseup", (e) => {
				e.preventDefault();
				mapZoomData.mouseOriginX = NaN;
				mapZoomData.mouseOriginY = NaN;
				mapZoomData.isDown = false;


			});

			canvas.current.addEventListener("mouseleave", (e) => {
				mapZoomData.mouseOriginX = NaN;
				mapZoomData.mouseOriginY = NaN;
				mapZoomData.isDown = false;
			});

			canvas.current.addEventListener("mousemove", (e) => {
				if (mapZoomData.isDown) {
					e.preventDefault();
					const {x: posX, y: posY} = transformMouseCoordsToCorrectCoords(e);
					mapZoomData.panX = posX - mapZoomData.mouseOriginX;
					mapZoomData.panY = posY - mapZoomData.mouseOriginY;
					updateCanvas();
				}
			});

			// This is for hover of clickables
			canvas.current.addEventListener("mousemove", (e) => {
				const {x: posX, y: posY} = transformMouseCoordsToCorrectCoordsWithCamera(e);


				let hover = false;

				for (const clickable of clickables) {
					if (posX >= clickable.x && posX <= clickable.x + clickable.w && posY >= clickable.y && posY <= clickable.y + clickable.h) {
						hover = true;
						if (canvas.current)
							canvas.current.style.cursor = "pointer";
					}
				}

				if (!hover) {
					if (canvas.current)
						canvas.current.style.cursor = "default";

				}
			});


			// This is for hover of clickables
			canvas.current.addEventListener("click", (e) => {
				const {x: posX, y: posY} = transformMouseCoordsToCorrectCoordsWithCamera(e);

				for (const clickable of clickables) {
					if (posX >= clickable.x && posX <= clickable.x + clickable.w && posY >= clickable.y && posY <= clickable.y + clickable.h) {
						clickable.onClick();
					}
				}
			});
		}
	});

	return (
		<canvas ref={canvas}/>
	);
}
