import {CardArray, CardFile, DefaultK12Importance} from './CardsTypes'; 

import quantities from './mapcards/quantities.json'; 
import natural from './mapcards/natural.json'; 
import integers from './mapcards/integers.json'; 
import rational from './mapcards/rational.json'; 
import real from './mapcards/real.json'; 
import complex from './mapcards/complex.json'; 
import modular from './mapcards/modular.json'; 
import padicint from './mapcards/padicint.json'; 
import padicrat from './mapcards/padicrat.json'; 
import formulas from './mapcards/formulas.json'; 
import perm from './mapcards/perm.json'; 
import realseq from './mapcards/realseq.json'; 
import integerseq from './mapcards/integerseq.json'; 
import graphs from './mapcards/graphs.json'; 
import quivers from './mapcards/quivers.json'; 
import cats from './mapcards/cats.json'; 
import labgraphs from './mapcards/labgraphs.json'; 
import weightgraphs from './mapcards/weightgraphs.json'; 
import posets from './mapcards/posets.json'; 
import booleans from './mapcards/booleans.json'; 
import dioph from './mapcards/dioph.json'; 
import realeq from './mapcards/realeq.json'; 
import ineq from './mapcards/ineq.json'; 
import realfunc from './mapcards/realfunc.json'; 
import realmat from './mapcards/realmat.json'; 
import poly from './mapcards/poly.json'; 
import maclaurin from './mapcards/maclaurin.json'; 
import ratex from './mapcards/ratex.json'; 
import laurent from './mapcards/laurent.json'; 
import trans from './mapcards/trans.json'; 
import combigeom from './mapcards/combigeom.json'; 
import euclidspaces from './mapcards/euclidspaces.json'; 
import euclidplane from './mapcards/euclidplane.json'; 
import kleingeom from './mapcards/kleingeom.json'; 
import curvedgeom from './mapcards/curvedgeom.json'; 
import fractalgeom from './mapcards/fractalgeom.json'; 
import {globalcards} from './GlobalCards';

function proc (obj : CardFile) : CardArray {
    const subcards : CardArray = {};

    obj.localcards.forEach((card) => {
        subcards[obj.id + "/" + card.id] = {
            type: "localcard",
            name: card.name,
            flavor: card.flavor,
            subtype: card.subtype,
            blurb: card.blurb,
            defaultK12Importance: card.defaultK12Importance as DefaultK12Importance
        };
    });

    const allCards : CardArray = {
        ...subcards,
        [obj.id]: {
            type: "mapcard",
            name: obj.name,
            defaultK12Importance: obj.defaultK12Importance as DefaultK12Importance,
            localcards: Object.keys(subcards)
        }
    };

    return allCards;

}


export const cards : CardArray = {
    ...globalcards,
    ...proc(quantities as CardFile), 
    ...proc(natural as CardFile), 
    ...proc(integers as CardFile), 
    ...proc(rational as CardFile), 
    ...proc(real as CardFile), 
    ...proc(complex as CardFile), 
    ...proc(modular as CardFile), 
    ...proc(padicint as CardFile), 
    ...proc(padicrat as CardFile), 
    ...proc(formulas as CardFile), 
    ...proc(perm as CardFile), 
    ...proc(realseq as CardFile), 
    ...proc(integerseq as CardFile), 
    ...proc(graphs as CardFile), 
    ...proc(quivers as CardFile), 
    ...proc(cats as CardFile), 
    ...proc(labgraphs as CardFile), 
    ...proc(weightgraphs as CardFile), 
    ...proc(posets as CardFile), 
    ...proc(booleans as CardFile), 
    ...proc(dioph as CardFile), 
    ...proc(realeq as CardFile), 
    ...proc(ineq as CardFile), 
    ...proc(realfunc as CardFile), 
    ...proc(realmat as CardFile), 
    ...proc(poly as CardFile), 
    ...proc(maclaurin as CardFile), 
    ...proc(ratex as CardFile), 
    ...proc(laurent as CardFile), 
    ...proc(trans as CardFile), 
    ...proc(combigeom as CardFile), 
    ...proc(euclidspaces as CardFile), 
    ...proc(euclidplane as CardFile), 
    ...proc(kleingeom as CardFile), 
    ...proc(curvedgeom as CardFile), 
    ...proc(fractalgeom as CardFile), 
};