import React, {useEffect, useState} from 'react';
import './App.css';
import { TopMapSVGWithCamera } from './map/Map';
import {Adventure, AdventureSection, ASShowAnswerTask, exampleAdventure, ASImage, ASCards, ASRichText, ASMultichoiceTask, ASIntegerTask, ASMapLocation, ASUnlockCards, ASYoutubeVideo} from './Model';
import {CardRack, MarkdownAndLatex} from './UtilComponents';
import {AdventureSectionEditor} from './Editor';


function App() {

  const emptyData : Adventure = {
    name: "Unnamed Adventure",
    sections: [],
  };
  const exampleData = exampleAdventure;
  const lsData = localStorage.getItem("data-adventure-editor");

  const [adventure, setAdventure] = useState<Adventure>(lsData != null ? JSON.parse(lsData) : exampleData);
  const [editing, setEditing] = useState<boolean>(false);

  useEffect(() => {
    localStorage.setItem("data-adventure-editor", JSON.stringify(adventure))
  }, [adventure]);


	useEffect(() => {
		if ((window as any).MathJax) {
			(window as any).MathJax.typeset();
		}
	});

  const toggleEdit = () => {
    setEditing(!editing);
  }

  const clearMaybe = (newData : Adventure) => {
    if (window.confirm("Are you sure you want to clear data?")) {
      setAdventure(newData);
    }
  }

  const clickDownloadHref = (href : string, download : string) => {
    const linkElement = document.createElement("a");

    linkElement.setAttribute("href", href);
    linkElement.setAttribute("download", download);

    linkElement.style.display = "none";
    document.body.appendChild(linkElement);

    linkElement.click();

    document.body.removeChild(linkElement);
  }

  const download = () => {
    const href = "data:text/plain;charset=utf-8," + encodeURIComponent(JSON.stringify(adventure, null, 4));

    clickDownloadHref(href, "adventure.json");
  }

  const upload = async (files : FileList | null) => {
    if (files == null) {
      return;
    }

    const fileText = await (files[0].text());
    const obj = JSON.parse(fileText);

    if (obj) {
      setAdventure(obj);
    }
  }

  let displaySection : JSX.Element[];

  if (editing) {
    displaySection = adventure.sections.map((section, i) => 
      <DisplayAdventureSectionEditable key={i} section={section} adventure={adventure} setAdventure={setAdventure} index={i}/>
    );
  } else {
    displaySection = adventure.sections.map((section, i) => 
      <DisplayAdventureSection key={i} section={section}/>
    )

  }



  return (
    <div className="App">
      <div className="controlCenter">
        <button onClick={(e) => toggleEdit()}>{editing ? "Preview" : "Edit"}</button>
        <button onClick={(e) => download()}>Download</button>
        <div className="fileUpload">
          Upload file: <br />
          <input type="file" onChange={(e) => upload(e.target.files)}></input>
        </div>
        <button onClick={(e) => clearMaybe(emptyData)}>Clear all the data</button>
        <button onClick={(e) => clearMaybe(exampleData)}>Load example</button>
      </div>
      <h1>{adventure.name}</h1>
      {editing ? <div> Title:<br /> <input type="text" value={adventure.name} onChange={(e) => setAdventure({...adventure, name: e.target.value})}></input> </div>: null}
      {displaySection}
    </div>
  );
}

function DisplayAdventureSectionEditable(props : {section : AdventureSection, index : number, adventure : Adventure, setAdventure : (newAdventure : Adventure) => void}) {

  return (
    <div className="adventureSectionEditable">
      <DisplayAdventureSection section={props.section} ></DisplayAdventureSection>
      <AdventureSectionEditor index={props.index} adventure={props.adventure} setAdventure={props.setAdventure}></AdventureSectionEditor>
    </div>
  );
}

function DisplayAdventureSection(props : {section : AdventureSection}) {

  const displays : {[key in AdventureSection["type"]]: () => JSX.Element} = {
    "richtext": () => <DisplayRichText section={props.section as ASRichText}/>,
    "image": () => <DisplayImage section={props.section as ASImage}/>,
    "ytvideo": () => <DisplayYoutubeVideo section={props.section as ASYoutubeVideo}/>,
    "cards": () => <DisplayCards section={props.section as ASCards}/>,
    "showanswertask": () => <DisplayShowAnswerTask section={props.section as ASShowAnswerTask}/>,
    "multichoicetask": () => <DisplayMultiChoiceTask section={props.section as ASMultichoiceTask}/>,
    "integertask": () => <DisplayIntegerTask section={props.section as ASIntegerTask}/>,
    "maplocation": () => <DisplayMapLocation section={props.section as ASMapLocation}/>,
    "unlockcards": () => <DisplayUnlockCards section={props.section as ASUnlockCards}/>,
  }

  return (
    <div className="adventureSection">
      {displays[props.section.type]()}
    </div>
  );
}

function DisplayRichText(props : {section : ASRichText}) {

  return (
    <MarkdownAndLatex content={props.section.content}></MarkdownAndLatex>
  );
}

function DisplayImage(props : {section : ASImage}) {

  return (
    <img src={props.section.src} alt={props.section.alt}></img>
  );
}

function DisplayYoutubeVideo(props : {section : ASYoutubeVideo}) {

  const [ytData, setYTData] = useState<any>("waiting");

  useEffect(() => {
    fetch("https://www.youtube.com/oembed?url=" + props.section.url).then((res) => res.json()).then((res) => {
      setYTData(res);
    });
  }, [props.section.url]);

  if (ytData === "waiting") {
    return (<p>Loading youtube video...</p>);
  } else if (!ytData.html) {
    return (<p>There was an error loading the youtube video.</p>);
  } else {
    //console.log(ytData);
    return (<div>
        <h3>Youtube video: {ytData.title}</h3>
        <div className="ytContainer" dangerouslySetInnerHTML={{__html: ytData.html}}>
        </div>
      </div>
    );
  }
}

function DisplayCards(props : {section : ASCards}) {

  return (
    <CardRack ids={props.section.ids}></CardRack>
  );
}

function DisplayShowAnswerTask(props : {section : ASShowAnswerTask}) {

  const [answerShown, setAnswerShown] = useState(false);


	useEffect(() => {
		if ((window as any).MathJax) {
			(window as any).MathJax.typeset();
		}
	}, [answerShown]);

  return (
    <div className="task showtask">
      <MarkdownAndLatex content={props.section.task}></MarkdownAndLatex>
      {answerShown ? (
        <MarkdownAndLatex content={props.section.answer}></MarkdownAndLatex>
      ) : (
        <button onClick={(e) => setAnswerShown(true)}>Show answer</button>
      )
      }
    </div>
  );
}

function DisplayMultiChoiceTask(props : {section : ASMultichoiceTask}) {

  const [currentAnswer, setCurrentAnswer] = useState<null | number>(null);

  const showCorrect = currentAnswer !== null;

  console.log(props.section);

  return (
    <div className="task choicetask">
      <MarkdownAndLatex content={props.section.task}></MarkdownAndLatex>
      <div className="choicetaskoptioncontainer">
      {
        props.section.answers.map((answer, i) => <div className="choicetaskoption" key={answer + " " + i} style={{backgroundColor: (showCorrect ? (props.section.correct[i] ? "#AAFFAA" : "#FFAAAA") : "inherit")}}>
          <MarkdownAndLatex content={answer}></MarkdownAndLatex>
          <button onClick={(e) => setCurrentAnswer(i)}>Select this</button>
          {showCorrect ? (<div>
            {props.section.correct[i] ? "Correct" : "Not correct"}
            </div>
            ) : null}
        </div>)
      }
      </div>
      {currentAnswer === null ? null : (props.section.correct[currentAnswer] ? "You are correct!" : "You are incorrect.")}
    </div>
  );
}

function DisplayIntegerTask(props : {section : ASIntegerTask}) {

  const [currentAnswer, setCurrentAnswer] = useState<number>(0);

  const [isCorrect, setIsCorrect] = useState<"unset" | boolean>("unset");

  const checkAnswer = () => {
    setIsCorrect(currentAnswer === props.section.correct);
  }

  return (
    <div className="task integertask" style={{backgroundColor: (isCorrect === "unset" ? "inherit" : (isCorrect ? "#AAFFAA" : "#FFAAAA"))}}>
      <MarkdownAndLatex content={props.section.task}></MarkdownAndLatex>
      <input type="number" min={0} max={1000} step={1} value={currentAnswer} onChange={(e) => setCurrentAnswer(e.target.valueAsNumber)}></input>
      <button onClick={(e) => checkAnswer()}>Check</button> <br />
      {isCorrect === "unset" ? null : (isCorrect ? "You are correct!" : "You are incorrect.")}
    </div>
  );
}

function DisplayMapLocation(props : {section : ASMapLocation}) {

  return (
    <TopMapSVGWithCamera width={800} height={600} markedLocation={[props.section.x, props.section.y]}></TopMapSVGWithCamera>
  );
}

function DisplayUnlockCards(props : {section : ASUnlockCards}) {

  return (
    <CardRack ids={props.section.ids} />
  );
}


export default App;
